* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p{
  padding: 5px;
  text-align: center;
}
 
.card img{
  max-height: 170px;
}
 
.card-grid ol{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  
}
 
.contenedor{
  max-width: 1200px;
  margin: 0 auto;
}